<template>
  <div id="single-blog">
    <h1>{{blog.title}}</h1>
    <article>{{blog.content}}</article>
    <p>作者：{{blog.author}}</p>
    <ul>
      <li v-for="category in blog.categories" :key="category.id">{{category}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'single-blog',
  data () {
    return {
      id:this.$route.params.id,
      blog:{}
    }
  },
  created(){
    this.$http.get('https://wd7057910966wvuxla.wilddogio.com/posts/'+this.id+'.json').then((data)=>{
      // console.log(this.id);
      // console.log(data);
      // this.blog=data.body;
      return data.json();
    }).then((data)=>{
      // console.log(data);
      return this.blog=data; 
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #single-blog{
    max-width: 960px;
    margin: 0 auto;
    background: #eee;
    border: 1px solid #333;
    padding: 10px;
  }
</style>



