<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submitted">
      <label>博客标题</label>
      <input type="text" v-model="blog.title">
      <label>博客内容</label>
      <textarea cols="30" rows="10" v-model="blog.content"></textarea>

      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label>Angular.js</label>
        <input type="checkbox" value="Angular.js" v-model="blog.categories">
      </div>

      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author.id">
          {{author}}
        </option>
      </select>
      <button type="button" v-on:click.prevent="post">提交</button>
    </form>

    <div v-if="submitted">
      <h4>您的博客发表成功!</h4>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{blog.title}}</p>
      <p>博客内容：</p>
      <p>{{blog.content}}</p>
      <p>博客分类：</p>
      <ul>
        <li v-for="category in blog.categories" :key="category.id">
          {{category}}
        </li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  created(){
    // this.authors[0]='dan';
  },
  data () {
    return {
      blog:{
        title:'',
        content:'',
        categories:[],
        author:''
      },
      authors:['dan','yao','ming','ying','rui'],
      submitted:false
    }
  },
  methods:{
    post:function(){
      this.$http.post('https://wd7057910966wvuxla.wilddogio.com/posts.json',this.blog).then((data)=>{
        console.log(data);
      });
      this.submitted=true;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}
#add-blog{
  border: 1px solid red;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
}
label{
  display: block;
  margin: 20px 0 10px;
}
input[type='text'],textarea,select{
  width: 100%;
  display: block;
  padding: 20px;
}
#checkboxes label{
  display: inline-block;
  margin-top: 0;
}
#checkboxes input{
  display: inline-block;
  margin-right: 10px;
}
button{
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border:0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#preview{
  padding: 10px 20px;
  border: 1px #333 dotted;
  margin: 30px 0;
}
#preview h3{
  margin-top: 10px;
}
</style>



